.header {
  height: 56px;
  display: flex;
  padding: 0 10px;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  box-shadow: inset 1px 1px 0 rgba(0,0,0,.1),inset 0 -1px 0 rgba(0,0,0,.07);

  .headerItem {
     height: 39.5px;
     padding: 10px 12px;
  }
}

.header:hover {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.page {
  background-color: blue;
}